// paw_select
	.lang_select {
		min-width: unset;
		border-radius: 8px 8px 0 0;
		padding: 8px 16px;
		font-size: 12px;
		.paw-current_value {
			border-radius: 0;
			border: none;
			display: flex;
			padding: 0 10px 0 0;
			min-width: 100px;
		}
		.paw-list {
			width: 100%;
			border-radius:0 0 8px 8px;
			max-height: 160px;
			overflow:auto;
			li {
				margin: 0 !important;
				&:first-of-type {
					.paw-list-item {
						padding-top: 10px;

					}
				}
			}
			&-item {
				display: flex;
				padding: 5px 16px;
				position: relative;
				width: 100%;
				opacity:.4;
				transition:.5s;
				color:#6D788C !important;
				&:after {
					content:'';
					display: block;
					width: 5px;
					height: 5px;
					border-radius:50%;
					background:#6D788C;
					position: absolute;
					top: 50%;
					right: 10px;
					transform: translate(0,-50%);
				}
				&:hover {
					opacity: 1;
				}
			}
			&::-webkit-scrollbar-track
			{
				-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
				border-radius: 10px;
				background-color: #F5F5F5;
			}

			&::-webkit-scrollbar
			{
				width: 5px;
				background-color: $middleblue;
			}

			&::-webkit-scrollbar-thumb
			{
				border-radius: 10px;
				-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
				background-color: $middleblue;
			}

		}
		&.active {
			background:#fff;
			color: #6D788C;
			.paw-current_value {
				color: $middleblue;
				&:after {
					content:'';
					display: block;
					width: 5px;
					height: 5px;
					border-radius:50%;
					background:$middleblue;
					position: absolute;
					top: 50%;
					right: 10px;
					transform: translate(0,-50%);
				}
			}
		}
	}

	.system_select {
		padding: 18px 20px;
		background-color:#fff;
		border-radius:8px;

		.paw-current_value {
			display: flex;
			align-items: center;
			border:none;
			padding: 0 40px 0 0;
			border-radius:0;
			color:$middleblue;
