				align-items: center;
				// white-space:nowrap;
				border:none;
				position: relative;
				width: 100%;
				justify-content: space-between;
				.system {
					width: 135px;
					margin-right: 10px;
					text-align: left;
					// .system_name {
					// 	text-overflow: ellipsis;
					// 	overflow:hidden;
					// 	position: relative;
						
					// }
				}
				// &:hover {
				// 	.system {
				// 		.system_name {
				// 			overflow:visible;
				// 		}
				// 	}
				// }
				&::after {
					content: "";
					display: block;
					width: 8px;
					height: 8px;
					border-radius:50%;
					background:$middleblue;
					position: absolute;
					top:50%;
					right: 35px;
					transform:translate(0,-50%);	
				}
				
			}
			li {
				background:#fff;
				&:nth-of-type(odd) {
					.paw-list-item {
						background: #F9FBFD;
						&:hover {
							.system {
								.system_name {
									background: #F9FBFD;
								}
							}
						}
					}
				}
				&:last-of-type {
					.paw-list-item {
						&:hover {
							.system {
								.system_name {
									background: #fff
								}
							}
						}
					}
				}
			}
		}
			
		.system {
			pointer-events:none;			
		}
		&.active {
		}
	}

	.filter_select {
		padding:10px 20px;
		border-radius: 8px;
		background:#fff;
		margin-right: 9px;
		box-shadow: 0px 5px 10px rgba(59, 119, 181, 0.05);
		.paw-current_value {
			display: flex;
			align-items: center;
			border:none;
			padding: 0 35px 0 0;
			border-radius:0;
			color:$middleblue;
			font-weight: 700;
			position: relative;
			background: url("data:image/svg+xml,%3Csvg width='16' height='8' viewBox='0 0 16 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L8 6.6L15 1' stroke='%2300A0FB'/%3E%3C/svg%3E%0A") calc(100% - 10px) 50%  no-repeat;
			&:before {
				content:attr(title);
				display: block;
				width: auto;
				height: auto;
				position: relative;
				margin-right: 5px;
			}

		}
		.paw-list {
			width: 100%;
			padding-top: 5px;
			border-radius: 0 0 8px 8px;
			overflow: hidden;
			&-item {
				width: 100%;
				background:#fff;
				border:none;
				padding:10px;

				&:hover {
					color:$middleblue;
				}

			}
			li {
				background:#fff;
				&:nth-of-type(odd) {
					.paw-list-item {
						background: #F9FBFD;
					}
				}
			}
		}
		
		&:last-of-type {
			margin-right: 0;
			margin-left: auto;
		}
	}

	.paw-list-item {
		* {
			pointer-events: none;
		}
	}
// paw_select

